<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Faq
      </div>
      <v-spacer></v-spacer>
      <v-btn color="primary" @click="create()" depressed elevation="2">
        <v-icon>mdi-plus</v-icon>
        Tambah</v-btn>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table class="elevation-1" :headers="headers" :items="data.data" loading-text="Loading... Please wait" :loading="data.loading"
        :search="search">
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.Answer`]="{ item }">
            <span v-html="item.Answer"></span>
        </template>
        <template #[`item.action`]="{ item }">
          <v-btn icon color="success" @click="detail(item)">
            <v-icon small>
              mdi-eye
            </v-icon>
          </v-btn>
          <v-btn icon color="warning" @click="edit(item)">
            <v-icon small>
              mdi-lead-pencil
            </v-icon>
          </v-btn>
          <v-btn icon color="red" @click="hapus(item.id)">
            <v-icon small>
              mdi-delete
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <Create/>
    <Update/>
    <Detail/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
import Detail from '@/components/Faq/Detail.vue'
import Update from '@/components/Faq/Update.vue'
import Create from '@/components/Faq/Create.vue'

export default {
  name: 'FaqPage',
  components: {
    Create, Update, Detail
  },
  data: () => ({
    search: '',
    headers: [
      {
        text: 'Kategori',
        align: 'start',
        sortable: false,
        value: 'Category'
      },
      { text: 'Pertanyaan', value: 'Question' },
      { text: 'Jawaban', value: 'Answer' },
      { text: 'Action', value: 'action', align: 'center', width: '170px' }
    ]
  }),
  mounted: function () {
    store.dispatch('faq/reqData')
  },
  computed: {
    ...mapState('faq', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('faq', [
      'edit', 'detail', 'hapus', 'create'
    ]),
    strippedHtml (data) {
      const regex = /(<([^>]+)>)/ig
      return data.replace(regex, '')
    }
  }
}
</script>
