<template>
  <v-row justify="center">
    <v-dialog v-model="state.dialogCreate" persistent fullscreen scrollable hide-overlay transition="dialog-bottom-transition">
      <v-card tile>
        <v-toolbar fixed dark color="primary">
          <v-btn icon dark @click="state.dialogCreate = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Create FAQ</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn color="yellow" text :loading="state.loading" @click="save(form)">
              SAVE
            </v-btn>
            <v-btn color="white" text :loading="state.loading" @click="state.dialogCreate = false">
              CLOSE
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-text>
          <v-container>
            <v-row dense class="mt-10">
              <v-col cols="12" md="12">
                <v-text-field label="Kategory" v-model="form.Category" outlined></v-text-field>
              </v-col>
              <v-col cols="12" md="12" mb="0">
                <v-text-field label="Pertanyaan" v-model="form.Question" outlined></v-text-field>
              </v-col>
              <v-col cols="12" md="12">
                <label for="">Jawaban</label>
                <tiptap-vuetify
                  v-model="form.Answer"
                  :extensions="extensions"
                  outlined
                  placeholder="Write something …"
                />
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>

        <div style="flex: 1 1 auto;"></div>
      </v-card>
    </v-dialog>

  </v-row>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import { TiptapVuetify, Heading, Bold, Italic, Strike, Underline, Code, Paragraph, BulletList, OrderedList, ListItem, Link, Blockquote, HardBreak, HorizontalRule, History } from 'tiptap-vuetify'

export default {
  name: 'CreateFaq',
  components: { TiptapVuetify },
  data () {
    return {
      extensions: [
        History,
        Blockquote,
        Link,
        Underline,
        Strike,
        Italic,
        ListItem,
        BulletList,
        OrderedList,
        [Heading, {
          options: {
            levels: [1, 2, 3]
          }
        }],
        Bold,
        Code,
        HorizontalRule,
        Paragraph,
        HardBreak
      ],
      // starting editor's content
      notifications: false,
      sound: true,
      widgets: false,
      alignments: [
        'start',
        'center',
        'end'
      ],
      form: {},
      items: [
        {
          text: 'Active', value: '1'
        },
        {
          text: 'No Active', value: '0'
        }
      ]
    }
  },
  inject: {
    theme: {
      default: { isDark: false }
    }
  },
  computed: {
    ...mapState('faq', {
      state: (state) => state
    })
  },
  methods: {
    ...mapActions('faq', [
      'save'
    ]),
    createBase64Image: function (FileObject) {
      const reader = new FileReader()
      reader.onload = (event) => {
        this.form.ImagesPath = event.target.result
      }
      reader.readAsDataURL(FileObject)
    }
  }
}
</script>
